import Webcam from "react-webcam";
import React, { Fragment, useEffect, useState } from "react";
import "./Challenge.css";
import { Client } from "https://cdn.jsdelivr.net/npm/@gradio/client/dist/index.min.js";
import ChallengeResultCard from "../../Components/ChallengeResultCard/ChallengeResultCard";

const Challenge = () => {

    const webcamRef = React.useRef(null);
    const [img, setImg] = useState('');
    const [result, setResult] = useState('');
    const [desc_text, setDesc_text] = useState("");
    const [isFlipped, setIsFlipped] = useState(false);


    const capture = React.useCallback(
        () => {
            const imageSrc = webcamRef.current.getScreenshot();
            console.log(imageSrc, desc_text);
            setImg(imageSrc)
        },
        [webcamRef]
    );

    const predict = async (url, desc_text) => {
        const client = await Client.connect("YoussefA7med/English_Helper_Written");
        setResult(
            await client.predict("/predict", {
                image_url: url,
                paragraph: desc_text,
                model: "meta-llama/llama-3.2-11b-vision-instruct:free",
            }));
        console.log(result);

    }

    const submitDescription = (e) => {
        e.preventDefault();
        predict(img, desc_text);
        result && setIsFlipped(true);
    }

    useEffect(() => {
        console.log(result);
    }, [result])

    return (
        <>
            <div className="container justify-content-center ">
                <div className={`row custom-card ${isFlipped ? "flipped" : ""}`}>
                    <div className="col-12   flip front">
                        <div className="row">
                            {img ?
                            <img src={img} alt="alt" />
                            :
                            < >
                                <div className="position-relative p-0 m-0 col-6">
                                    <Webcam
                                        audio={false}
                                        ref={webcamRef}
                                        screenshotFormat="image/jpeg"
                                        className=" rounded p-0 m-0"
                                    />
                                    <i onClick={capture} className="fa-solid fa-circle fa-rotate-90 fs-1 position-absolute bottom-0 start-50 translate-on-x" style={{ "color": "#dc3545", "transform": "transla" }}></i>
                                </div>
                            </>
                        }
                        <div className="col-6">
                        <form onSubmit={submitDescription} className="d-flex flex-column gap-2 ">
                            <label htmlFor="description">Description</label>
                            <textarea className="border-2 rounded-2 " id="description" placeholder="Enter your description" onChange={(e) => setDesc_text(e.target.value)} required>
                            </textarea>
                            <button className="btn btn-lg bg-danger text-white border-0 rounded-pill " >Capture photo</button>

                        </form>
                        </div>
                        </div>
                    </div>
                    <div className="col-12  flip back">
                        {result && <ChallengeResultCard result={result.data} />}
                    </div>
                </div>
            </div>
        </>
    )
}

export default Challenge;